.app{
	display: flex;
	
}

.top button {
    display: inline-block;
    border: 4px solid #84dccf;
    padding: 1rem 2rem;
    margin: 0;
    text-decoration: none;
    background: #84dccf;
    color: #000000;
    font-family: "Montserrat";
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease,
                color 250ms ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.top button:hover{
    background: rgba(0,0,0,0);
    color: #84dccf
}


.top button:active {
    transform: scale(0.99);
}

.search-list{
    background-color: #84dccf;
    min-height:100%;
}
.search-list li{
	padding-top:5px;
	padding-bottom:5px;
	cursor:pointer;
	transition: background-color 150ms;
}
.search-list li:hover{
	background-color: #95eddf;
}
.not-player{
	text-align: center;
	color: #84dccf;
	border:5px solid #84dccf;
}
.song-title{
	font-size: 35px;
	text-align: center;
}
.song-artist{
	font-size: 30px;
	text-align: center;
}
.song-anime{
	font-size: 25px;
	text-align: center;
}
.react-player{
	padding:10px;
}

.song-list-head{
	font-size: 25px;
	text-align: center;
	color: #84dccf;
}

.song-list-val{
	padding-left: 5px;
	background-color: #84dccf;
	cursor:pointer;
	transition: background-color 150ms;
}
.song-list-val:hover{
