.playlist-main{
	display: flex;
}
table{
	table-layout: fixed;
	width: 50vw;
	background-color: #84dccf;
}
tr{
	cursor:pointer;
	transition: background-color 150ms;
}
tr:hover{
	background-color: #95eddf
}
th,td{
	border-bottom: 1px solid #000000;
}
.song-title2{
	margin-top: 10px;
	background-color: #84dccf;
	font-size: 35px;
	text-align: center;
}
.song-artist2{
	background-color: #84dccf;
	font-size: 30px;
	text-align: center;
}
.song-anime2{
	background-color: #84dccf;
	font-size: 25px;
	text-align: center;
	
}
.lyrics{
	border: 5px solid #84dccf;;
	color: #84dccf;
	text-align: center;	
}