@import url(https://fonts.googleapis.com/css?family=Ubuntu);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
html{
    height: 100%;
	background-color:#312F2F;
	margin:0px;
	padding:0px;
}
body {
    height: 100%;
	font-family: "Ubuntu";
	font-size: 20px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.top{
	text-align: center;
}
.header{
	font-family: "Montserrat";
	font-weight: bold;
	color: #84dccf;
	font-size: 75px;
	border: 5px solid #84dccf;
}
input[type=submit] {
    display: inline-block;
    border: 4px solid #84dccf;
    margin: 0;
    text-decoration: none;
    background: #84dccf;
    color: #000000;
    font-family: "Montserrat";
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease,
                color 250ms ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
}

button:hover{
    background: rgba(0,0,0,0);
    color: #84dccf;
}

.app{
	display: flex;
	
}

.top button {
    display: inline-block;
    border: 4px solid #84dccf;
    padding: 1rem 2rem;
    margin: 0;
    text-decoration: none;
    background: #84dccf;
    color: #000000;
    font-family: "Montserrat";
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease,
                color 250ms ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.top button:hover{
    background: rgba(0,0,0,0);
    color: #84dccf
}


.top button:active {
    transform: scale(0.99);
}

.search-list{
    background-color: #84dccf;
    min-height:100%;
}
.search-list li{
	padding-top:5px;
	padding-bottom:5px;
	cursor:pointer;
	transition: background-color 150ms;
}
.search-list li:hover{
	background-color: #95eddf;
}
.not-player{
	text-align: center;
	color: #84dccf;
	border:5px solid #84dccf;
}
.song-title{
	font-size: 35px;
	text-align: center;
}
.song-artist{
	font-size: 30px;
	text-align: center;
}
.song-anime{
	font-size: 25px;
	text-align: center;
}
.react-player{
	padding:10px;
}

.song-list-head{
	font-size: 25px;
	text-align: center;
	color: #84dccf;
}

.song-list-val{
	padding-left: 5px;
	background-color: #84dccf;
	cursor:pointer;
	transition: background-color 150ms;
}
.song-list-val:hover{
	background-color: #95eddf;
}

.middle button {
    display: inline-block;
    border: 4px solid #84dccf;
    margin: 0;
    text-decoration: none;
    background: #84dccf;
    color: #000000;
    font-family: "Montserrat";
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease,
                color 250ms ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.middle button:hover{
    background: rgba(0,0,0,0);
    color: #84dccf
}

.playlist-main{
	display: flex;
}
table{
	table-layout: fixed;
	width: 50vw;
	background-color: #84dccf;
}
tr{
	cursor:pointer;
	transition: background-color 150ms;
}
tr:hover{
	background-color: #95eddf
}
th,td{
	border-bottom: 1px solid #000000;
}
.song-title2{
	margin-top: 10px;
	background-color: #84dccf;
	font-size: 35px;
	text-align: center;
}
.song-artist2{
	background-color: #84dccf;
	font-size: 30px;
	text-align: center;
}
.song-anime2{
	background-color: #84dccf;
	font-size: 25px;
	text-align: center;
	
}
.lyrics{
	border: 5px solid #84dccf;;
	color: #84dccf;
	text-align: center;	
}
